.gridContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.ueberschrift {
  text-align: center;
}

.gridItem {
  flex: 1 1 30%;
  margin: 5%;
  display: flex;
  justify-content: center;
}

.thumbnail {
  width: 100%;
  height: 80vh;
  border: none;
  cursor: pointer;
}

.modalBackdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  position: relative;
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 800px; /* Ensure max-width for larger screens */
}

.closeButton {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.modalIframe {
  width: 100%;
  height: 80vh;
  border: none;
}

/* Media Queries */
@media (max-width: 768px) {
  .gridItem {
    flex: 1 1 100%;
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .thumbnail {
    width: 80%;
    height: 500px;
  }
}

@media (max-width: 480px) {
  .gridItem {
    flex: 1 1 100%;
    display: flex;
    justify-content: center;
  }

  .modal {
    width: 100%;
    height: 100%;
    border-radius: 0;
  }

  .modalIframe {
    height: calc(100vh - 40px); /* Adjust height for mobile view */
  }
}
